<template>
  <div class="container">
    <div class="flex">
      <div class="txt">原始密码</div>
      <input type="text" placeholder="请输入原始密码">
    </div>
    <div class="flex">
      <div class="txt">短信验证码</div>
      <input type="text" placeholder="请输入短信验证码">
      <span>发送手机验证码</span>
    </div>
    <div class="flex">
      <div class="txt">新密码</div>
      <input type="text" placeholder="请输入新密码">
    </div>
    <div class="flex">
      <div class="txt">确定新密码</div>
      <input type="text" placeholder="请再次输入新密码">
    </div>
    <div class="submit" @click="submit">提交</div>
  </div>
</template>

<script>
export default {
  data () {
    return {
    }
  },

  components: {},

  computed: {},

  mounted: function() {},

  methods: {
    submit() {

    }
  }
}
</script>

<style lang='scss' scoped>
  .container {
    width: 400px;
    padding: 40px;
    .flex {
      padding: 10px;
      border-bottom: 1px solid #999;
      margin-bottom: 10px;
      position: relative;
      input {
        border: none;
        text-align: right;
      }
      span {
        position: absolute;
        top: 10px;
        right: -120px;
        font-size: 12px;
        background: #2B4365;
        color: #fff;
        padding: 4px 8px;
        border-radius: 4px;
        cursor: pointer;
      }
    }
    .submit {
      margin-top: 50px;
      background: #2B4365;
      text-align: center;
      color: #fff;
      line-height: 40px;
      border-radius: 10px;
      cursor: pointer;
    }
  }
</style>



